import Vue from 'vue'
export default {
    data(){
        return{
            code:this.$route.query.code
        }
    },
    methods:{
        copyCode(){
            var code = this.$refs.code;
            debugger
            code.select();
            try {
                if( document.execCommand("Copy")){
                    Vue.$vux.toast.show({text:"复制成功"});
                }else{
                    throw DOMException;
                }
            }catch (e){
                Vue.$vux.toast.show({text:"复制失败",type:"warn"});
            }


        }
    }
}



